          <v-divider></v-divider>

          <v-list flat subheader three-line>
            <v-subheader>{{ $t("common.general") }}</v-subheader>

            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>{{ $t("common.theme") }}</v-list-item-title>

                <v-sheet class="ml-3"
                  ><v-switch
                    inset
                    v-model="$vuetify.theme.dark"
                    :label="$t('common.dark')"
                  ></v-switch>
                </v-sheet>
                <!-- <v-select
                  v-model="selectTheme"
                  :items="themeList"
                  item-text="label"
                  item-value="value"
                  label="Select"
                  persistent-hint
                  return-object
