              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>{{ $t("common.lang") }}</v-list-item-title>
                <v-select
                  v-model="selectLocale"
                  :items="localeList"
                  item-text="label"
                  item-value="locale"
                  label="Select"
                  persistent-hint
                  return-object
