<script>
import { actionTypes } from "@/store/store-types";
export default {
  name: "SettingView",
  data() {
    return {
      selectLocale: null,
      localeList: [
        {
          label: this.$t("lang.en"),
          locale: "en",
        },
        {
          label: this.$t("lang.zhHant"),
          locale: "zhHant",
        },
      ],
      // selectTheme: null,
      // themeList: [
      //   {
      //     label: this.$t("common.light"),
      //     value: "light",
      //   },
      //   {
      //     label: this.$t("common.dark"),
      //     value: "dark",
      //   },
      // ],
    };
  },
  mounted() {
    this.selectLocale = this.localeList.find(
      (x) => x.locale === this.$i18n.locale
    );
  },
  watch: {
    selectLocale(newValue, oldValue) {
      if (newValue !== this.$i18n.locale) {
        this.$store.dispatch(actionTypes.setLang, newValue.locale);
      }
    },
  },
};
</script>
